import React, { useState } from "react";
import RegisterForm from "./components/RegisterForm";
import RegistrationList from "./components/RegistrationList";
import "./App.css";


function App() {
  const [refresh, setRefresh] = useState(false);

  // Function to trigger a refresh after successful registration
  const fetchRegistrations = () => {
    setRefresh(!refresh); // This toggles the refresh state
  };

  return (
    <div>
      <h1>Gujjar Gaur Brahaman Samaj Bhatkhedi</h1>
      <h2>This Website is Developed By Shivam Purohit.</h2>
      <RegisterForm fetchRegistrations={fetchRegistrations} />
      <RegistrationList refresh={refresh} />
    </div>
  );
}

export default App;
