import React, { useState } from "react";
import axios from "axios";

const RegisterForm = ({ fetchRegistrations }) => {
  const [formData, setFormData] = useState({ 
    sno: "", 
    name: "", 
    members: "", 
    totalRupees: "", 
    remark: "" 
  });

  const [error, setError] = useState("");

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // Clear previous error
      setError("");

      // Send data to backend
      await axios.post("http://localhost:5000/api/registrations", formData);

      // Reset form after successful registration
      setFormData({ sno: "", name: "", members: "", totalRupees: "", remark: "" });

      // Refresh the registration list
      fetchRegistrations();
    } catch (err) {
      if (err.response && err.response.data && err.response.data.error) {
        setError(err.response.data.error); // Show error message from the backend
      } else {
        setError("An error occurred while submitting the form.");
      }
    }
  };

  return (
    <div className="form-container">
      <h2>Gautam Jayanti Registration 2025</h2>
      <form onSubmit={handleSubmit} className="registration-form">
        <input
          type="number"
          name="sno"
          placeholder="S.No"
          value={formData.sno}
          onChange={handleChange}
          required
        />
        <input
          type="text"
          name="name"
          placeholder="Name"
          value={formData.name}
          onChange={handleChange}
          required
        />
        <input
          type="number"
          name="members"
          placeholder="Number of Members"
          value={formData.members}
          onChange={handleChange}
          required
        />
        <input
          type="number"
          name="totalRupees"
          placeholder="Total Rupees"
          value={formData.totalRupees}
          onChange={handleChange}
          required
        />
        <input
          type="text"
          name="remark"
          placeholder="Remark (Optional)"
          value={formData.remark}
          onChange={handleChange}
        />
        <button type="submit">Register</button>
      </form>

      {error && <div className="error-message">{error}</div>}
    </div>
  );
};

export default RegisterForm;
