import React, { useEffect, useState } from "react";
import axios from "axios";
import { jsPDF } from "jspdf";
import "jspdf-autotable";
import html2canvas from "html2canvas";  // Import html2canvas

const RegistrationList = ({ refresh }) => {
  const [registrations, setRegistrations] = useState([]);
  const [editingRegistration, setEditingRegistration] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [successMessage, setSuccessMessage] = useState("");

  // Fetch registrations from the backend
  const fetchRegistrations = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await axios.get("http://localhost:5000/api/registrations");
      setRegistrations(response.data);
    } catch (err) {
      setError("Failed to fetch registrations. Please try again.");
    }
    setLoading(false);
  };

  useEffect(() => {
    fetchRegistrations();
  }, [refresh]);

  // Handle editing a registration
  const handleEditClick = (reg) => {
    setEditingRegistration(reg);
  };

  // Handle form submission for editing registration
  const handleEditSubmit = async (e) => {
    e.preventDefault();
    const updatedRegistration = {
      sno: e.target.sno.value,
      name: e.target.name.value,
      members: e.target.members.value,
      totalRupees: e.target.totalRupees.value,
      remark: e.target.remark.value || "N/A",
      year: e.target.year.value,
    };

    try {
      await axios.put(
        `http://localhost:5000/api/registrations/${editingRegistration._id}`,
        updatedRegistration
      );
      fetchRegistrations(); // Refresh data
      setSuccessMessage("Registration updated successfully.");
      setEditingRegistration(null); // Close form
    } catch (error) {
      setError("Error updating registration. Please try again.");
    }
  };

  // Confirm and delete a registration
  const handleDeleteClick = async (id) => {
    const confirmDelete = window.confirm("Are you sure you want to delete this registration?");
    if (confirmDelete) {
      try {
        await axios.delete(`http://localhost:5000/api/registrations/${id}`);
        fetchRegistrations(); // Refresh data
        setSuccessMessage("Registration deleted successfully.");
      } catch (error) {
        setError("Error deleting registration. Please try again.");
      }
    }
  };

  // Handle PDF download
  const handleDownloadPDF = () => {
    if (registrations.length === 0) {
      alert("No data available to generate PDF.");
      return;
    }
  
    const doc = new jsPDF();
    doc.text("Gautam Jayanti Registration List 2025", 14, 10);
  
    const tableColumn = ["S.No", "Name", "Members", "Total Rupees", "Remark"];
    const tableRows = registrations.map((reg) => [
      reg.sno,
      reg.name,
      reg.members,
      reg.totalRupees,
      reg.remark || "N/A",
    ]);
  
    // Ensure autoTable plugin is available
    if (typeof doc.autoTable === "function") {
      doc.autoTable({
        head: [tableColumn],
        body: tableRows,
        startY: 20,
        theme: "grid",
      });
  
      doc.save("registration_list.pdf");
    } else {
      console.error("autoTable is not a function. Please check the jspdf-autotable installation.");
    }
  };

  // Handle table print
  const handlePrint = () => {
    const printWindow = window.open("", "_blank");

    const content = document.createElement("div");
    content.style.padding = "20px";
    
    const title = document.createElement("h2");
    title.innerText = "Gautam Jayanti Registration List 2025";
    content.appendChild(title);

    const table = document.createElement("table");
    table.style.width = "100%";
    table.style.borderCollapse = "collapse";
    const thead = document.createElement("thead");
    const headerRow = document.createElement("tr");
    ["S.No", "Name", "Members", "Total Rupees", "Remark"].forEach((column) => {
      const th = document.createElement("th");
      th.style.border = "1px solid #000";
      th.style.padding = "8px";
      th.style.textAlign = "left";
      th.innerText = column;
      headerRow.appendChild(th);
    });
    thead.appendChild(headerRow);
    table.appendChild(thead);

    const tbody = document.createElement("tbody");
    registrations.forEach((reg) => {
      const row = document.createElement("tr");
      ["sno", "name", "members", "totalRupees", "remark"].forEach((field) => {
        const td = document.createElement("td");
        td.style.border = "1px solid #000";
        td.style.padding = "8px";
        td.style.textAlign = "left";
        td.innerText = reg[field] || "N/A";
        row.appendChild(td);
      });
      tbody.appendChild(row);
    });
    table.appendChild(tbody);

    content.appendChild(table);

    const totalsDiv = document.createElement("div");
    totalsDiv.style.marginTop = "20px";
    totalsDiv.innerHTML = `
      <p><strong>Total Registrations:</strong> ${registrations.length}</p>
      <p><strong>Total Members:</strong> ${registrations.reduce((sum, reg) => sum + reg.members, 0)}</p>
      <p><strong>Total Amount:</strong> ${registrations.reduce((sum, reg) => sum + reg.totalRupees, 0)} Rs</p>
    `;
    content.appendChild(totalsDiv);

    printWindow.document.body.appendChild(content);
    printWindow.document.close();
    printWindow.print();
  };

  // Handle Image Download
  // Handle Image Download
// Handle Image Download
// Handle Image Download
const handleDownloadImage = () => {
    const table = document.getElementById("registration-table");
    if (!table) {
      alert("Table not found!");
      console.error("Table element not found.");
      return;
    }
  
    // Use html2canvas to capture the table as an image
    html2canvas(table, {
      useCORS: true,  // Enable CORS if you're using external images
    }).then((canvas) => {
      const imgData = canvas.toDataURL("image/png");
      const link = document.createElement("a");
      link.href = imgData;
      link.download = "registration_list.png";
      link.click();
    }).catch((error) => {
      console.error("Error while generating image:", error);
      alert("An error occurred while generating the image. Check the console for details.");
    });
  };
  
  
  
  

//   const calculateTotals = () => {
//     let totalRegistrations = registrations.length;
//     let totalMembers = registrations.reduce((sum, reg) => sum + reg.members, 0);
//     let totalAmount = registrations.reduce((sum, reg) => sum + reg.totalRupees, 0);
//     return { totalRegistrations, totalMembers, totalAmount };
//   };

//   const { totalRegistrations, totalMembers, totalAmount } = calculateTotals();

  return (
    <div className="registration-list">
      <h2>Gautam Jayanti Registration List 2025</h2>

      {loading && <div>Loading...</div>}
      {error && <div style={{ color: "red" }}>{error}</div>}
      {successMessage && <div style={{ color: "green" }}>{successMessage}</div>}

      <button onClick={handleDownloadPDF}>Download PDF</button>
      <button onClick={handlePrint}>Print</button>
      <button onClick={handleDownloadImage}>Download Image</button>

      <table id="registration-table">
        <thead>
          <tr>
            <th>S.No</th>
            <th>Name</th>
            <th>Members</th>
            <th>Total Rupees</th>
            <th>Remark</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {registrations.map((reg) => (
            <tr key={reg._id}>
              <td>{reg.sno}</td>
              <td>{reg.name}</td>
              <td>{reg.members}</td>
              <td>{reg.totalRupees}</td>
              <td>{reg.remark || "N/A"}</td>
              <td>
                <button onClick={() => handleEditClick(reg)}>Edit</button>
                <button onClick={() => handleDeleteClick(reg._id)}>Delete</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* Edit Form */}
      {editingRegistration && (
        <div className="edit-form">
          <h3>Edit Registration</h3>
          <form onSubmit={handleEditSubmit}>
            <label>S.No</label>
            <input type="text" name="sno" defaultValue={editingRegistration.sno} required />

            <label>Name</label>
            <input type="text" name="name" defaultValue={editingRegistration.name} required />

            <label>Members</label>
            <input type="number" name="members" defaultValue={editingRegistration.members} required />

            <label>Total Rupees</label>
            <input type="number" name="totalRupees" defaultValue={editingRegistration.totalRupees} required />

            <label>Remark</label>
            <input type="text" name="remark" defaultValue={editingRegistration.remark} />

            <label>Year</label>
            <input type="text" name="year" defaultValue={editingRegistration.year} required />

            <button type="submit">Update</button>
            <button type="button" onClick={() => setEditingRegistration(null)}>Cancel</button>
          </form>
        </div>
      )}
    </div>
  );
};

export default RegistrationList;
